<script>
    import data from "./../data/sheet.csv"
    import content from "./../data/doc.json"
    import {Container,Row,Col,TabContent, TabPane,Spinner} from 'sveltestrap'
    import {fade} from 'svelte/transition'
    import viewport from "../stores/viewport.js"
    import {onMount} from 'svelte'
    import inView from '../actions/inView.js'
   let open = false;
   let loading = true;   

   function toggle(direction) {
       if (direction == 'in') {
            setTimeout(function() {
                open = true;
            }, 2000);
       } else {
           open = false;
       }
   }

   export let filter = 'all';
   export let sort = 'abc';
   export let mode = 'full';
   let search;
   let filteredData = data;
   let sortedData = filteredData;
   function filterGrid () {
       if (search) {
           filteredData = data.filter(function (i,n) {
               for (var key in filter) {
                   if (i.candidate_fn.toLowerCase().includes(search.toLowerCase()) || i.candidate_ln.toLowerCase().includes(search.toLowerCase()) || i.riding.toLowerCase().includes(search.toLowerCase()))
                   return true;
               }
               return false;
           })
       } else if (filter == 'all') {
           filteredData = data;
       } else {
           filteredData = data.filter(function (i,n) {
               return i.party_ml.indexOf(filter) > -1;
           });
       }
       sortedData = filteredData.sort(function(a,b) {
                return a.candidate_ln.localeCompare(b.candidate_ln);
            })
   }
   onMount(() => {
       setTimeout(function() {
           loading = false;
       }, 200);
       filterGrid();
   });
   
</script>

{#if loading} 

   <div id="spinner">
       <Spinner color=primary/>
   </div>

{:else}

<Container>
       <Row>
           <Col sm=12 lg={{size:4,offset:2}}>
               <div class="control">
                       <label for="cat">Filter by party:</label>

                       <!-- svelte-ignore a11y-no-onchange -->
                       <select bind:value={filter} on:change="{() => filterGrid()}" name="party" id="party">
                            <option value="all">All</option>
                           <option value="lib">Liberal Party</option>
                           <option value="con">Conservative Party</option>
                           <option value="ndp">NDP</option>
                           <option value="green">Green Pparty</option>
                           <option value="bq">Bloc québécois</option>
                           <option value="other">Other</option>
                       </select>
               </div> 
           </Col>
           <Col sm=12 lg={{size:4}}>
               <div class="control">
                       <label for="search">Search for a candidate or riding:</label>

                       <!-- svelte-ignore a11y-no-onchange -->
                       <input type="text" bind:value={search} on:keyup="{() => filterGrid()}" name="search" id="order">
               </div> 
           </Col>
       </Row>
       <Row>
           <Col sm=12>
               <div class="candidates-grid" use:inView on:enter={() => toggle('in')} on:exit={() => toggle('out')}>
               {#each sortedData as candidate}
                   <div class="candidate-box">
                    <div class="candidate-content">

                        <h4>{candidate.candidate_fn} {candidate.candidate_ln} </h4>
                        {#if candidate.party.indexOf('Liberal') > -1}
                            <img src="assets/libs.png" alt="Liberal party of Canada">
                        {:else if candidate.party.indexOf('Conservative') > -1}
                            <img src="assets/cons.png" alt="Conservative party of Canada">
                        {:else if candidate.party.indexOf('Democratic') > -1}
                            <img src="assets/ndplogo.png" alt="New Democratic Party">
                        {:else if candidate.party.indexOf('Green') > -1}
                            <img src="assets/greens.png" alt="Green party of Canada">
                        {:else if candidate.party.indexOf('Bloc') > -1}
                            <img src="assets/bq.png" alt="Bloc québécois">
                        {:else}
                            <p>{candidate.party}</p>
                        {/if}
                        <p><i>{candidate.riding}</i></p>
                    </div>
                        {#if candidate.date_of_pledge}
                            <div class="pledge pledge-yes">
                                PLEDGED TO <br />STOP TMX
                            </div>
                        {:else}
                            <div class="pledge pledge-no">
                                HAS NOT PLEDGED <br />TO STOP TMX
                            </div>
                        {/if}
                   </div>
               {/each}
               </div>
           </Col>  
       </Row>
   </Container>

   {#if open}
        <div class="ett-cta">
            {@html content.voter_pledge}
            <a href="https://act.stand.earth/page/32477/petition/1?ea.tracking.id=referrals" target="_new">Sign the pledge >></a>
        </div>
    {/if}
{/if}


