   .candidates-grid {
       display:flex;
       flex-flow: row wrap;
       justify-content: center;
       text-align:center;
   }

   .candidate-box {
       width:290px;
       min-height:133px;
       position:relative;
       text-align:center;
       border:1px solid #cccccc;
       margin-right:20px;
       margin-bottom:20px;
   }

   .candidate-content {
        padding:20px;
        min-height:150px;
   }

   .candidate-box h4 {
       font-family: 'Gotham';
       color:var(--stand-tertiary);
   }

   .candidate-box p {
       margin-bottom:0px;
   }
   

   .candidate-box img {
       max-width:100px;
       max-height:100px;
       padding-top:10px;
       padding-bottom:10px;
       margin:auto;

   }

   .control select, .control input {
       width:100%;
       margin-bottom:30px;
   }

   #spinner {
   width:100%;
   height:200px;
   margin-top:10%;
   text-align:center;
 }

 .pledge {
    padding:10px;
    font-family:'Gotham';
    text-transform:uppercase;
    color:var(--white);
 }

 .pledge-yes {
     
     background-color:green;
     border:1px solid green;

 }
 .pledge-no {
     background-color:grey;
     border:1px solid grey;
 }

 .ett-cta {
     background: rgba(181,18,27,1);
     color:var(--white);
     padding:20px;
     position:fixed;
     bottom:0px;
     right:0px;
 }

 .ett-cta a {
     float:right;
     background:var(--white);
     color:var(--stand-secondary);
     padding:10px;
     text-decoration:none;
     font-family:'Gotham';
     text-align:center;
 }
</style>
